<template>
    <div class="users">
      <h1>Users</h1>
      <!--&lt;!&ndash;用户信息添加&ndash;&gt;-->
      <form action="" @submit="addUser">
        <input type="text" v-model="user.name" placeholder="姓名">
        <input type="text" v-model="user.email" placeholder="电子邮件">
        <input type="submit"  value="Submit">
      </form>
      <!--用户信息展示-->
      <ul>
        <li v-for="(user,index) in users">
          <span><strong>{{user.name}}</strong></span>：
          <span>{{user.email}}</span>
          <span class="delete" @click="deleteLi(index)">Delete</span>
        </li>
      </ul>
    </div>
</template>
<!--
传值：string number boolean
引用：array abject
-->
<script>
    export default {
        name: 'users',
        data () {
            return {
              user:{},
              users:[]
            }
        },
      methods:{
            addUser(e){
            this.users.push({
                name:this.user.name,
                email:this.user.email
            });
          e.preventDefault();
        },
        deleteLi(index){
            this.users.splice(index,1)
        }
      },
      created(){
        // GET /someUrl
        this.$http.get('http://jsonplaceholder.typicode.com/users').then(response => {
        // 获取数据
        // this.someData = response.body;
        const res = response.body;
        if (res) {
          this.users = res
        }
      }, response => {
        // 错误回调
        alert('请求失败')
      })
      }
    }

</script>
<style scoped>

.users{
  max-width:1200px;
  padding:0 20px;
  margin:40px auto;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
ul{
  list-style: none;
}
li{
  padding:10px 0;
}
  .delete{
    padding:2px 6px;
    color: #fff;
    background: #f80;
    cursor:pointer;
  }
</style>



