<template>
  <div class="row"  id="translateForm">
      <div class="center col-lg-6 col-lg-offset-3">
        <h1>在线翻译</h1>
        <h4 class="text-muted">改变自我，挑战自我，从现在开始。</h4>
        <form class="bs-example bg-white" action="" @submit="formSubmit">
          <input class="col-lg-6 form-control" v-model="textToTranslate" type="text" placeholder="输入需要翻译的内容">
          <select class="col-lg-3 form-control" v-model="language">
            <option class="col-lg-3" v-for="option in options" v-bind:value="option.value">{{option.text}}</option>
          </select>
          <input class="btn btn-info" type="submit" value="翻译">
        </form>
      </div>
    </div>




</template>
<script>

  export default {
    name: 'translateForm',
    data(){
      return {
        textToTranslate:'',
        language:'en',
        options:[
          {text:'英语',value:'en'},
          {text:'中文',value:'zh'},
          {text:'俄语',value:'ru'},
          {text:'法语',value:'fr'},
          {text:'日语',value:'ja'},
        ]
      }
    },
    methods:{
      formSubmit(e){
        this.$emit('formSubmit',[this.textToTranslate,this.language]);
        e.preventDefault()
      }
    },
    components:{

    },
    created(){

    }
  }
</script>
<style >
.center{
  margin:0 auto;
  padding-top: 20px;
  text-align: center;
}
 #translateForm form input,#translateForm form select{
    display: inline-block;
    margin-bottom: 10px;
  }
</style>



