<template>
  <div id="app">
    <translateForm @formSubmit="transFormText($event)"></translateForm>
    <translateOutput :text="translateText"></translateOutput>
  </div>
</template>

<script>
  import TranslateForm from './components/TranslateForm'
  import TranslateOutput from './components/TranslateOutput'
  export default {
    name: 'App',
    data(){
      return {
        translateText:''
      }
    },
    methods:{
      transFormText($event){
        // GET /someUrl
        this.$http.get('https://translate.yandex.net/api/v1.5/tr.json/translate?key=trnsl.1.1.20180129T113647Z.19c5823172066bd4.66c66b918e64e745fcec85723b9e7c835783c84f&lang='+$event[1]+'&text='+$event[0]).then(response => {
          // this.someData = response.body;
          const res = response.body;
          if (res) {
            this.translateText = res.text[0]
          }
        }, response => {
          // 错误回调
          alert('请求失败')
        })      }
    },
    components:{
      'translateForm':TranslateForm,
      'translateOutput':TranslateOutput,
    }
  }
</script>
<style >

</style>



