<template>
    <header @click="changeTitle">
      <h1>{{title1}}{{title}}</h1>
    </header>
</template>

<script>
    export default {
        name: 'app-header',
      props:{
        title:{
            type:String,
        }
      },
        data () {
            return {
                title1:'Vue.js Dome'
            }
        },
      methods:{
        changeTitle:function(){
//            this.title = 'Change'
//       子组件向父组件传值    注册一个事件  $(事件名称，要传的值)
          this.$emit('titleChange','子向父组件传值')
        }
      },
     /* beforeCreate:function(){
        alert('组件实例化之前')
      },
      created:function(){
        alert('组件实例创建完毕，属性已经绑定但是DOM还未生成')
      },
      beforeMount:function(){
        alert('组件挂载前,页面未展示，但是虚拟DOM已经配置');
      },
      mounted:function(){
        alert('组件挂载后，此方法执行后，DOM页面显示');
      },
      beforeUpdate:function(){
        alert('组件更新之前，页面仍未跟新，但是虚拟DOM已经配置');
      },
      update:function(){
        alert('组件更新，页面展示');
      },
      beforeDestroy:function(){
          alert('组件销毁前')
      },
      destroyed:function(){
          alert('组件销毁后')
      }*/
    }
</script>
<style scoped>
  header{
    padding:10px;
    background:lightgreen;
  }
  h1{
    color: #222;
    text-align:center;
  }
</style>



