<template>
    <footer>
      <p>{{copyright}}</p>
      <p>{{title}}</p>
    </footer>
</template>

<script>
    export default {
        name: 'app-footer',
        props:{
          title:{
              type:String
          }
        },
        data () {
            return {
                copyright:'Copyright 2017 Vue Dome'
            }
        }
    }
</script>
<style scoped>
  footer{
    padding:6px;
    background: #222;
  }
  p{
    color: #fff;
    text-align: center;
  }
</style>



