<!--html模板-->
<template>
  <div id="home">
    <!--titleChange找到这个事件执行updateTitle方法$event等于子组件要传给父组件的值-->
    <app-header :title="title" @titleChange="updateTitle($event)"></app-header>
    <app-footer :title="title"></app-footer>
  </div>
</template>
<!--行为逻辑-->
<script>
  import Header from './Header'
  import Footer from './Footer'
  export default {
    name: 'home',
    data(){
      return {
        title:'传值（string number boolean）',
        users:[]
      }
    },
    methods:{
      updateTitle:function(title){
        this.title = title
      }
    },
    components:{
      'app-header':Header,
      'app-footer':Footer
    },
    created:function(){

    }
  }
</script>
<!--样式-->
<style >
  h1{
    color:#f60;
  }
</style>



